<template><section><h1>Select</h1>
<template>
    <coding title="基础" content="简单的select" :code="data">
        <y-select v-model="a">
            <y-option value="1" label="a"></y-option>
            <y-option value="2" label="b"></y-option>
            <y-option value="3" label="c"></y-option>
        </y-select>
    </coding>
    <coding title="清除选项" content="在y-select中，设定clearable值，即可显示清除按钮" :code="data1">
        <y-select v-model="b" clearable="">
            <y-option value="1" label="a"></y-option>
            <y-option value="2" label="b"></y-option>
            <y-option value="3" label="c"></y-option>
        </y-select>
    </coding>
    <coding title="禁用选项" content="在y-option中，设定disabled值，即可禁用改选项" :code="data2">
        <y-select v-model="c">
            <y-option value="1" label="a" disabled></y-option>
            <y-option value="2" label="b"></y-option>
            <y-option value="3" label="c"></y-option>
        </y-select>
    </coding>
    <coding title="禁用select" content="在y-select中，设定disabled值，即可禁用改选项" :code="data2">
        <y-select v-model="c" disabled>
            <y-option value="1" label="a" disabled></y-option>
            <y-option value="2" label="b"></y-option>
            <y-option value="3" label="c"></y-option>
        </y-select>
    </coding>
</template>

<h2>参数设置</h2>
<h3>select</h3>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定的数据（必选）</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>clearable</td>
<td>是否出现删除按钮</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否将该分组下所有选项置为禁用</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>option</h3>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>选项的标签，若不设置则默认与 value 相同</td>
<td><code>String</code>, <code>Number</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>选项的值</td>
<td><code>String</code>, <code>Number</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用该选项</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            a:"1",
            b:"",
            c:"",
data:
`<y-select v-model="value">
    <y-option value="1" label="a"></y-option>
    <y-option value="2" label="b"></y-option>
    <y-option value="3" label="c"></y-option>
</y-select>`,
data1:
`<y-select v-model="value"  clearable>
    <y-option value="a" label="a"></y-option>
    <y-option value="b" label="b"></y-option>
    <y-option value="c" label="c"></y-option>
</y-select>`,
data2:
`<y-select v-model="value" disabled>
    <y-option value="a" label="a" disabled></y-option>
    <y-option value="b" label="b"></y-option>
    <y-option value="c" label="c"></y-option>
</y-select>`
        }
    }
}
</script>


